<template>
  <main id="todo-app">
    <header class="app-header">
      <h1>{{title}}</h1>
    </header>

    <section class="input-form">
      <span @click="allDone"> <i class="fa fa-sort-desc fa-lg" aria-hidden="true"></i> </span>
      <input type="text" v-model="inputTodo" @keyup.enter="addTodo" placeholder="What's your plan?">
    </section>

    <section class="todo-list">
      <ul>
        <li class="todo-item" v-for="(item, index) in filteredTodos" :key="index">
          <div class="view" v-show="!item.isEditing" @dblclick="item.isEditing=true">
            <div class="toggle-btn">
              <input class="custom-checkbox" :id="index" type="checkbox" v-model="item.isDone">
              <label :for="index"></label>            
            </div>
            <div class="item-div" v-bind:class="{done: item.isDone}" >{{item.todoContent}}</div>
            <span class="delete-btn" @click="removeTodo(item)"><i class="fa fa-times" aria-hidden="true"></i></span>
          </div>
          <input class="editInput" type="text" v-show="item.isEditing" @blur="item.isEditing=false" @keyup.enter="item.isEditing=false" v-model="item.todoContent" v-focus>
        </li>
      </ul>      
    </section>

    <section class="tool-bar">
      <span class="leftItems">还剩下 {{leftItems}} 件</span>
      <ul class="tool-bar-list">
        <li><input id="btn-all" type="radio" value="all" v-model="visibility"><label for="btn-all">全部</label></li>
        <li><input id="btn-active" type="radio" value="active" v-model="visibility"><label for="btn-active">正在做</label></li>
        <li><input id="btn-done" type="radio" value="done" v-model="visibility"><label for="btn-done">已做完</label></li>
      </ul>
      <button class="cleanDone-btn" @click="cleanDone">清除已完成</button>
    </section>

    <footer>
      <p>Written by Allenmind</p>
      <p>鼠标双击词条进行编辑</p>
      <p>按Enter进行确认</p>
    </footer>      

  </main>
</template>

<script>
  var STORAGE_KEY = 'my_vue2.0_todo_app';
  var itemsStorage = {
    // 获取Web存储中的数据
    fetch: function() {
      var items = JSON.parse(localStorage.getItem(STORAGE_KEY) || '[]');
      // console.log(items);
      return items;
    },
    // 保存数据
    save: function(items) {
      localStorage.setItem(STORAGE_KEY, JSON.stringify(items));
    }
  }

  var filter = {
    'all': function(todos) {
      return todos;
    },
    'active': function(todos) {
      return todos.filter(function(todo) {
        return !todo.isDone;
      });
    },
    'done': function(todos) {
      return todos.filter(function(todo) {
        return todo.isDone;
      });
    }
  };

  export default {
    name: 'todo-app',
    data() {
      return {
        title: 'Todos',
        inputTodo: '',
        items: itemsStorage.fetch(),
        allDoneToggle: false,
        visibility: 'all'
      }
    },

    watch: {
      // 深度watcher（每个属性的值都跟踪【类似深拷贝？】）
      items: {
        handler: function(items) {
          itemsStorage.save(items);
        },
        deep: true
      }
    },

    computed: {
      leftItems: function() {
        var count = 0;
        for (var i=0;i<this.items.length;i++) {
          if (!this.items[i].isDone) {
            count++;
          }
        }
        return count;
      },
      filteredTodos: function() {
        return filter[this.visibility](this.items);
      }
    },

    methods: {
      addTodo: function() {
        this.items.push({
          todoContent: this.inputTodo,
          isDone: false,
          isEditing: false
        });
        this.inputTodo = '';
      },
      removeTodo: function(item) {
        this.items.splice(this.items.indexOf(item), 1);
      },
      allDone: function() {
        if (!this.allDoneToggle) {
          for (var i=0;i<this.items.length;i++) {
            this.items[i].isDone = true;
          }
          this.allDoneToggle = true;          
        } else {
          for (var i=0;i<this.items.length;i++) {
            this.items[i].isDone = false;
          }
          this.allDoneToggle = false;  
        }
      },
      cleanDone: function() {
        this.items = filter['active'](this.items);
      }

    },
    directives: {
      focus: function(el) {
        el.focus();
      }
    }
  }
</script>

<style scoped>
  @import 'https://cdn.bootcss.com/font-awesome/4.7.0/css/font-awesome.min.css';

  /* reset */
  header > h1 {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    color: rgba(255, 90, 0, .3);
    font-size: 5em;
  }

  ul {
    list-style: none;
    padding-left: 0;
  }

  input[type="text"],
  button {
    outline: none;
    -webkit-appearance: none;
    border-radius: 0;
    border: none;
  }

  /* 布局 */
  .app-header {
    grid-area: h;
    justify-self: center;
  }
  
  .input-form {
    grid-area: if;
  }

  .todo-list {
    grid-area: tl;
  }

  .tool-bar {
    grid-area: tb;
  }

  footer {
    grid-area: f;
    justify-self: center;
  }

  #todo-app {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: 170px 50px auto 55px 50px;
    grid-template-areas: 
      ". . . . h h h h . . . ."
      ". . . . if if if if . . . ."
      ". . . . tl tl tl tl . . . ."
      ". . . . tb tb tb tb . . . ."
      ". . . . f f f f . . . .";     
  }

  /* .input-form */
  .input-form {
    display: flex;
    flex-direction: row;
    flex-wrap: none;
  }

  .input-form > span {
    display: inline-block;
    width: 10%;
    align-self: center;
    text-align: center;
  }

  .input-form > span:hover {
    cursor: pointer;
  }

  .input-form > input {
    width: 88%;
    height: 50px; 
    font-size: 1.4em;
  }

  /* .todo-list */
  .input-form,
  .todo-list,
  .todo-item,
  .tool-bar {
    background: white;
    box-shadow: 0px 0px 3px #aaa;
  }

  .todo-list > ul {
    margin: 0;
  }

  .todo-item > .view {
    padding: 1.1em 0;
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  .todo-item > .editInput {
    padding: 1em 0;
    font-size: 1.3em;
    margin-left: 14%;
    width: 86%;
    box-shadow: 0px 0px 4px #777;
  }

  .toggle-btn input {
    visibility: hidden;
  }

  .toggle-btn {
    width: 10%;
    margin-left: 1.1em;
    position: relative;
  }

  .toggle-btn label {
    cursor: pointer;
    position: absolute;
    width: 25px;
    height: 25px;
    top: -3px;
    left: 0;
    background: #eee;
    border:1px solid #ddd;
    border-radius: 100%;
  }

  .toggle-btn label:after {
    opacity: 0.1;
    content: '';
    position: absolute;
    width: 9px;
    height: 5px;
    background: transparent;
    top: 6px;
    left: 7px;
    border: 3px solid #00cc66;
    border-top: none;
    border-right: none;

    transform: rotate(-45deg);
  }

  .toggle-btn label:hover::after {
    opacity: 0.5;
  }

  .toggle-btn input[type=checkbox]:checked + label:after {
    opacity: 1;

  }

  .item-div {
    word-wrap: break-word;
    width: 80%;
    font-size: 1.4em;
    transition: all 0.5s;
  }

  .done {
    text-decoration: line-through;
    color: #aaa;
  }  

  .delete-btn {
    /*visibility: hidden;*/
    color: #aaa;
    width: 10%;
  }

  .delete-btn:hover {
    cursor: pointer;
  }

  /* .tool-bar */
  .tool-bar {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 1.1em;
  }

  .leftItems,
  .tool-bar-list,
  .tool-bar-list > li,
  .cleanDone-btn {
    display: inline-block;
  }

  .leftItems {
    color: #aaa;
    font-size: 0.8em;
    width: 20%;
  }

  .tool-bar-list {
    width: 60%;
  }

  .tool-bar-list > li {
    font-size: 0.9em;
    align-self: center;
  }

  .cleanDone-btn {
    background: transparent;
    border: 1px solid #aaa;
    border-radius: 5px;
    color: #aaa;
    width: 20%;
  }

  .cleanDone-btn:hover {
    cursor: pointer;
  }

  /* footer */
  footer {
    margin-top: 20px;
    margin-bottom: 150px;
    color: #aaa;
    text-align: center;
  }

</style>


